<template>
<header class="header">
        <a class="toggle" @click="toggleMenu" v-if="!hideToggle"> 
                <i class="fa fa-lg" :class="icon"></i>
        </a>
        <h1 class="title">
       
           <router-link to="/">{{ title }}</router-link>
        </h1>

        <UserDropdown v-if="!hideUserDropdown" />
</header>
    
</template>

<script>

import UserDropdown from './UserDropdown'

export default {
  components: { UserDropdown },
 
        name: 'Header',
        props: {
                title: String,
                hideUserDropdown: Boolean 
        } ,
        computed: {
                icon() {
                        return this.$store.state.isMenuVisible ? "fa-angle-left" : "fa-angle-down" //icone da setinha
                }
        }  ,
        methods: {
                toggleMenu() {
                        this.$store.commit('toggleMenu') //botão setinha
                }
        }
}
</script>

<style>

.header {
     grid-area: header;
     background: linear-gradient(to right, #16222A, #3A6073);

     display: flex;
     justify-content: center;
     align-items: center;
}

.title {
        font-size: 1.2rem;
        color: #fff;
        font-weight: 100;
        flex-grow: 1;
        text-align: center;
}

.title a {
        color: #fff;
        text-decoration: none;
}
.title a:hover {
        color: #fff;
        text-decoration: none;
}

header.header > a.toggle {
        width: 60px;
        height: 100%;
        color: #fff;
        justify-self: flex-start;
        text-decoration: none;

        display: flex;
        justify-content: center;
        align-items: center;
}

header.header > a.toggle:hover {
        background-color: rgba(0, 0, 0, 0.2);
        color: #fff;
}



</style>